import React from "react"
import { Link, useStaticQuery, graphql } from "gatsby"
import Card from "react-bootstrap/Card";
import Img from "gatsby-image";

import 'bootstrap/dist/css/bootstrap.min.css';

import Layout from "../components/Layout"
import Image from "../components/image"
import SEO from "../components/seo"
import BoxHongHuaRho from "../images/box-hong-hua-rho.jpg"

export const query = graphql`
query {
  file(relativePath: { eq: "box-hong-hua-rho.jpg" }) {
    childImageSharp {
      fluid(quality: 90) {
        ...GatsbyImageSharpFluid
      }
    }
  }
}
`;

export default function IndexPage({ data }) {
  return (
    <Layout>
      <SEO title="Home" />
      <h1 className="display-1"></h1>
      <p className="lead">What we offer and how you can get it.</p>
      <Card style={{ width: '18rem' }}>
        <Img className="card-img-top" fluid={data.file.childImageSharp.fluid} />
        <Card.Body>
          <Card.Title>Hong Shao Rou Box</Card.Title>
          <Card.Subtitle className="mb-2 text-muted">Red roast pork belly</Card.Subtitle>
          <Card.Text>
            This edition of our dinner box features hong hua rho, stir-fried veggies and of course rice.
        </Card.Text>
          <Card.Link href="#">Can I order this?</Card.Link>
        </Card.Body>
      </Card>
    </Layout>
  );
}
